import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { getParticipatelotsAction } from '../Redux/Actions/Actions';

function ParticipatLotList(props) {

    const [customerDetails, setcustomerDetails] = useState(null);
    const [participateLots, setParticipateLots] = useState(null);

    useEffect(() => {
        if (props.loginUserDetailsReducer?.loginUserDetails) {
            setcustomerDetails(props.loginUserDetailsReducer.loginUserDetails);
        }
    }, [props.loginUserDetailsReducer])

    useEffect(() => {
        if (customerDetails?.id) {
            props.getParticipatelotsAction(customerDetails.id);
        }
    }, [customerDetails])

    useEffect(() => {
        if (props.getCustimerParticipateLotsReducer?.customerParticipateLots) {
            setParticipateLots(props.getCustimerParticipateLotsReducer?.customerParticipateLots);
        }
    }, [props.getCustimerParticipateLotsReducer])

    return (
        <>
            <h3>Participate Lots</h3>
            {props.getCustimerParticipateLotsReducer.Loading ?
                <div className="alert alert-default bg-warning fade show w-100" role="alert">
                    <span className="alert-inner--text"><strong>Please Wait.</strong></span>
                </div> :
                participateLots ? participateLots.map((lot) =>
                    <>

                        <div className="border-light card col-lg-12 col-md-12 col-sm-12 p-0 text-darker" key={lot.id}>
                            <div className="card-header p-0">
                                <div className="card-title bg-gradient-lighter h5 mb-0 px-3">{lot.title}</div>
                                {/* <div className="">{lot}</div> */}
                            </div>
                            <div className="card-body p-1">
                                <ul className="nav nav-fill flex-column flex-md-row" id="tabs-icons-text" role="tablist">
                                    {/* <li className="nav-item bg-info border">
                                    Lot  : {lot.title}
                                </li> */}
                                    <li className="nav-item p-0 m-0 bg-translucent-light border row" >
                                        <div className="nav-item m-0 bg-primary border-right p-0 twoInOne1">Lot Amount  : {lot.amount}</div>
                                        <div className="nav-item m-0 bg-warning border-right p-0 twoInOne1">Participate Fees  : {lot.participate_fee}</div>
                                    </li>
                                    <li className="nav-item p-0 m-0 bg-translucent-light border row" >
                                        <div className="nav-item m-0 bg-primary border-right p-0 twoInOne1">Date  : {lot.StartDate}</div>
                                        <div className="nav-item m-0 bg-warning border-right p-0 twoInOne1">Status : {lot.lot_status}</div>
                                    </li>

                                </ul>
                            </div>
                        </div>
                        <hr className="my-2" />
                    </>

                )
                    : <div className="alert alert-default bg-warning fade show w-100" role="alert">
                        <span className="alert-inner--text"><strong>No Lot Availabel.</strong></span>
                        <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
            }
        </>)
}
const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
        getCustimerParticipateLotsReducer: state.getCustimerParticipateLotsReducer,
    }
}

const mapDispatchtoProps = {
    getParticipatelotsAction: (id) => getParticipatelotsAction(id),
}

export default connect(mapStateToProps, mapDispatchtoProps)(ParticipatLotList)