import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { logOutAction, isAllradyLoginAction, getUserDetailsAction } from "../Redux/Actions/Actions";
import appLogo from "../appLogo.png";

function NevbarComponent(props) {

    const [showManu, onShowManu] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        if (!props.loginUserDetailsReducer.loginUserDetails && localStorage.getItem('UserDetails') && localStorage.getItem('UserDetails') != '') {
            props.isAllradyLoginAction(JSON.parse(localStorage.getItem('UserDetails')));
        }
        else if (!props.loginUserDetailsReducer.loginUserDetails) {
            // navigate('/')
        }
    }, [props.loginUserDetailsReducer])

    const onLogOut = () => {
        navigate('/')
        props.logOutAction();
        window.location.reload();
    }

    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-dark bg-default p-0">
                <div className="container">
                    <Link className="navbar-brand" to={'/'}>
                        <img src={appLogo} style={{ width: '150px', height: '50px', }} />
                    </Link>
                    <div className="navbar-toggler h4" type="button" data-toggle="collapse" data-target="#nav-inner-success" aria-controls="nav-inner-success" aria-expanded="false" aria-label="Toggle navigation">
                        {props.loginUserDetailsReducer.loginUserDetails ?
                            <button className="btn text-white" onClick={() => onShowManu(!showManu)}> {props.loginUserDetailsReducer.loginUserDetails.name}</button> :
                            <Link to={'/login'} className="btn btn-block btn-default h-100" >Login</Link>
                        }
                    </div>
                    <div className={(showManu ? 'show' : '') + " collapse navbar-collapse"} id="nav-inner-success">
                        <div className="navbar-collapse-header">
                            <div className="row">
                                <div className="col-6 collapse-brand  ">
                                    <Link to='/userprofile' className="dropdown-item">Profile</Link>
                                    {/* <button className="btn btn-sm" onClick={() => onShowManu(false)}>X</button> */}
                                </div>
                                <div className="col-6 collapse-close">
                                    <button type="button" className="btn btn-sm" onClick={() => { onShowManu(false) }}>
                                        X </button>
                                </div>
                            </div>
                        </div>
                        <ul className="navbar-nav ml-lg-auto">
                            <li className="nav-item dropdown">
                                {props.loginUserDetailsReducer.loginUserDetails ?
                                    <div className="nav-link dropdown-toggle nav-link-icon p-0 h4 hidecomponent">
                                        <button className="btn btn text-white" onClick={() => onShowManu(!showManu)}> {props.loginUserDetailsReducer.loginUserDetails.name}</button>
                                    </div>
                                    : < Link to={'/login'} className="btn btn-block btn-default h-100" >Login</Link>
                                }
                                <div className={(showManu ? 'show' : '') + " dropdown-menu dropdown-menu-right "} onMouseLeave={() => onShowManu(!showManu)}>
                                    {/* <Link to='/userprofile' className="dropdown-item">Profile</Link> */}
                                    <Link to='/payments' className="dropdown-item">Payments</Link>
                                    <a className="dropdown-item">Customer Support</a>
                                    <div onClick={onLogOut} className="dropdown-item">Logout</div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
            {props.loginUserDetailsReducer?.loginUserDetails && !props.loginUserDetailsReducer?.loginUserDetails?.status &&
                <div className="alert alert-default bg-warning fade show row" role="alert">
                    <span className="alert-inner--text col-md-8 col-sm-12"><strong>Accout is not Active, Reqiest To Activeate Account.</strong></span>
                    <div className="col-md-4 col-sm-12 d-flex justify-content-end ">
                        <button className="btn btn-sm btn-primary " >
                            Reqiest To Activeate Account
                        </button>
                    </div>
                </div>
            }
        </>
    )

}

const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
    }
}
const mapDispatchtoProps = {
    logOutAction: () => logOutAction(),
    isAllradyLoginAction: (details) => isAllradyLoginAction(details),
}
export default connect(mapStateToProps, mapDispatchtoProps)(NevbarComponent)