import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import HeaderComponent from "./HeaderComponent";
import { getLotsAction } from '../Redux/Actions/Actions';
import FooterComponent from "./FooterComponent";
import NevbarComponents from "./NevbarComponents";
import HomeComponent from "./HomeComponent";

function AllLotsComponent(props) {

    const [lotList, setLotList] = useState(null);

    useEffect(() => { props.getLotLostAction() }, [])

    useEffect(() => { if (props.lotList) { setLotList(props.lotList) } }, [props.lotList])

    return (
        <div className="">
            {/* <NevbarComponents /> */}
            <HomeComponent />

            <div className="container-fluid " >
                <div className="row justify-content-md-center text-center">

                    {lotList ? lotList.map((lot) =>
                        <div className="card col-lg-5 col-md-5 col-sm-12 p-0 m-2 border-light" key={lot.id}>
                            <div className="card-header p-0">
                                <div className="card-title bg-gradient-lighter h6 mb-1">{lot.title}</div>
                                <div className="">{lot.company}</div>
                            </div>
                            <div className="card-body p-0">
                                <button type="button" className="btn btn-sm btn-primary btn-tooltip">{lot.materialLocation || ' - '}</button>
                                <button type="button" className="btn btn-sm btn-primary btn-tooltip">{lot.lot_status || ' - '}</button>
                                <div className="border-bottom pb-1">
                                    <div className="btn btn-sm btn-secondary btn-tooltip" >Total Quentity : {lot.Quantity || ' - '}</div>
                                    <button type="button" className="btn btn-sm btn-secondary btn-tooltip" >Price : {lot.Price || ' - '}</button>
                                </div>
                                <div className="border-bottom pb-1">
                                    <button type="button" className="btn btn-sm btn-secondary btn-tooltip" > Date : {lot.StartDate || ' - '} </button>
                                </div>
                                <div className="border-bottom pb-1">
                                    {lot.others && lot.others.map((other, index) =>
                                        <button type="button" className="btn btn-sm btn-secondary btn-tooltip" key={index}>{other}</button>
                                    )}
                                </div>
                            </div>
                        </div>
                    )
                        :
                        <div></div>
                    }
                </div>
            </div>

            <FooterComponent />
        </div >
    )
}
const mapStateToProps = (state) => {
    return {
        lotList: state.getLotsListReducer.lotsList,
        loginUserDetailsReducer: state.loginUserDetailsReducer,
    }
}
const mapDispatchtoProps = {
    getLotLostAction: () => getLotsAction(),
}
export default connect(mapStateToProps, mapDispatchtoProps)(AllLotsComponent)