import React, { useState } from "react";
import axios from "axios";
import { Services } from "../Redux/Actions/Services"
import NevbarComponent from './NevbarComponents';
import WinLotList from "./WinLotList";
import { connect } from "react-redux";
import ParticipatLotList from "./ParticipatLotList";
import Balance from "./Balance";
import FooterComponent from "./FooterComponent";

function PaymentPanel(props) {
    const [showWinLots, setShowWinLots] = useState(0)

    return (
        <div className="">
            <NevbarComponent />
            <div className="container-fluid">
                <div className="row mt-2" >
                    <div className="col-4 px-1">
                        <button className="btn btn-primary w-100 px-0" onClick={(() => setShowWinLots(0))}>Balance</button>
                    </div>
                    <div className="col-4 px-1">
                        <button className="btn btn-primary w-100 px-0" onClick={(() => setShowWinLots(1))}>Win</button>
                    </div>
                    <div className="col-4 px-1">
                        <button className="btn btn-primary w-100 px-0" onClick={(() => setShowWinLots(2))}>Participated</button>
                    </div>
                </div>
                {showWinLots == 0 &&
                    < Balance />
                }
                {showWinLots == 1 &&
                    <WinLotList />}
                {showWinLots == 2 &&
                    <ParticipatLotList />}
            </div>
            <FooterComponent />
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
    }
}
const mapDispatchtoProps = {
}
export default connect(mapStateToProps, mapDispatchtoProps)(PaymentPanel)
