import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { getCustomerBalanceAction } from '../Redux/Actions/Actions';
import { Services } from "../Redux/Actions/Services";

function loadScript(src) {
    return new Promise((resolve) => {

        const script = document.createElement("script");
        script.src = src;
        script.onload = () => {
            resolve(true);
        };
        script.onerror = () => {
            resolve(false);
        };
        document.body.appendChild(script);
    });
}

function getOrderData(param) {
    return Services.postService('getidforbalance', param).then(
        (response) => {
            if (response.data.sucess) {
                return response.data;
            }
            return false;
        }
    );
}

function Balance(props) {

    const [addBalance, setAddBalance] = useState(false);
    const [newBalance, setNewBalance] = useState(null);
    const [showError, setshowError] = useState(null);
    const [customerDetails, setcustomerDetails] = useState(null);
    const [balaceList, setBalaceList] = useState(null);
    const [processing, setProcessing] = useState(null);

    useEffect(() => {
        if (props.loginUserDetailsReducer?.loginUserDetails) {
            setcustomerDetails(props.loginUserDetailsReducer.loginUserDetails);
        }
    }, [props.loginUserDetailsReducer])

    useEffect(() => {
        if (customerDetails?.id) {
            props.getCustomerBalanceAction(customerDetails.id);
        }
    }, [customerDetails])

    useEffect(() => {
        if (props.getCustomerBalanceReducer?.customerBalance?.balanceHistory) {
            setBalaceList(props.getCustomerBalanceReducer?.customerBalance?.balanceHistory);
        }
    }, [props.getCustomerBalanceReducer])

    async function showRazorpay() {
        if (Number(newBalance) >= 3000) {
            const __DEV__ = document.domain === 'localhost';

            const src = await loadScript('https://checkout.razorpay.com/v1/checkout.js');
            if (!src) {
                // console.log('Razorpay script failed to load.');
                alert('Razorpay script failed to load.');
                return;
            }
            const param = {
                customerId: customerDetails.id,
                amount: newBalance,
            }
            const orderDetails = await getOrderData(param);
            if (orderDetails?.orderDetails.id) {
                const options = {
                    key: __DEV__ ? "rzp_test_F7NG4rFYLmPvVR" : 'BeGgxdMgYfnVmDiWtkfNTPVs',
                    amount: orderDetails.orderDetails.amount,
                    currency: orderDetails.orderDetails.currency,
                    name: orderDetails.custoemrDetais.name,
                    description: "Balance Payment.",
                    image: {},
                    order_id: orderDetails.orderDetails.id,
                    handler: async function (response) {
                        const data = {
                            orderCreationId: orderDetails.orderDetails.id,
                            razorpayPaymentId: response.razorpay_payment_id,
                            razorpayOrderId: response.razorpay_order_id,
                            razorpaySignature: response.razorpay_signature,
                        };

                        let result = Services.postService('addcustomerblance', param).then(
                            (response) => {
                                if (response.data.sucess) {
                                    props.getCustomerBalanceAction(customerDetails.id);
                                    setAddBalance(null);
                                    setProcessing(null)
                                    return response.data;
                                }
                                return false;
                            }
                        );
                    },
                    prefill: {
                        name: orderDetails.custoemrDetais.name,
                        email: orderDetails.custoemrDetais.email,
                        contact: orderDetails.custoemrDetais.contactNo,
                    },
                    notes: {
                        address: orderDetails.custoemrDetais.address,
                    },
                    theme: {
                        color: "#61dafb",
                    },
                };

                const paymentObject = new window.Razorpay(options);
                paymentObject.open();
            }
            else {
                alert('Something Wrong Happened, Try After Some Time.')
            }
        } else {
            setshowError('Balance Muset Be Graterthen 3000.')
        }
    }

    return (
        <>
            <h3>Balance </h3>
            <div className="card col-lg-12 col-md-12 col-sm-12 border-light p-0 my-2">
                <div className="card-header bg-light h6 d-flex justify-content-between ">
                    <span className="card-title m-0">Balance : {props.getCustomerBalanceReducer?.customerBalance?.lastBalance?.finalAmount}</span>
                    <button type="button" className="btn btn-sm btn-success btn-tooltip" data-animation="true" onClick={() => { setAddBalance(!addBalance) }}>
                        Add Balance
                    </button>
                </div>
            </div>
            {props.getCustomerBalanceReducer.Loading || processing ?
                <div className="alert bg-warning text-center">
                    <span className="alert-inner--text"><strong>Please Wait.</strong>
                    </span>
                    <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
                </div>
                :
                addBalance ?
                    <div className="card col-lg-12 col-md-12 col-sm-12 border-light p-0 my-2">
                        <div className="card-header bg-light h6  ">
                            <h5 className="d-block font-weight-bold ">Add Balance</h5>
                        </div>
                        <div className="card-body ">
                            <div className="form-group">
                                {showError &&
                                    <div className="alert alert-default bg-warning fade show w-100" role="alert">
                                        <span className="alert-inner--text"><strong>{showError}</strong></span>
                                    </div>
                                }
                                <input type="text" placeholder="Balance" name="newBalance" className="form-control"
                                    value={newBalance} onChange={(event) => {
                                        setNewBalance(event.target.value)
                                        setshowError(null)
                                    }} />
                            </div>
                            <button type="button" className="btn  btn-primary btn-tooltip" data-animation="true" onClick={() => {
                                setProcessing(true)
                                showRazorpay()
                            }}>
                                Add Balance
                            </button>

                            <button type="button" className="btn btn-danger btn-tooltip" data-animation="true" onClick={() => { setAddBalance(!addBalance) }}>
                                Back
                            </button>
                        </div>
                    </div>
                    :
                    balaceList ? balaceList.map((balance) =>
                        <>
                            <div className="card col-lg-12 col-md-12 col-sm-12 border-light p-0 my-2 border-dark" key={balance.id}>
                                <div className="card-body p-0">
                                    <ul className="list-unstyled nav-fill text-default mb-0 text-capitalize" id="tabs-icons-text" role="tablist">
                                        <li className="nav-item bg-info  ">
                                            Date  : {balance.created_at}
                                        </li>
                                        <li className="nav-item p-0 m-0 bg-translucent-light  border row" >

                                            {balance.action == 'credit'
                                                ?
                                                <div className="nav-item m-0 bg-success  border-right p-0 twoInOne1">
                                                    Action  : {balance.action}
                                                </div>
                                                :
                                                <div className="nav-item m-0 bg-warning border-right p-0 twoInOne1">
                                                    Action  : {balance.action}
                                                </div>
                                            }
                                            {balance.action == 'Participate Fees' &&
                                                <div className="nav-item m-0 bg-warning border-right p-0 twoInOne1">
                                                    Lot  : {balance.title}
                                                </div>

                                            }
                                            {balance.action == 'credit'
                                                ?
                                                <div className="nav-item m-0 bg-success  border-right p-0 twoInOne1">
                                                    Amount  : {balance.actionAmount}
                                                </div>
                                                :
                                                <div className="nav-item m-0 bg-warning border-right p-0 twoInOne1">
                                                    Amount  : {balance.actionAmount}
                                                </div>
                                            }

                                        </li>


                                        <li className="nav-item bg-primary text-white">
                                            Final Amount  : {balance.finalAmount}
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <hr className="m-0" />
                        </>)
                        :
                        <div className="alert alert-default bg-warning fade show w-100" role="alert">
                            <span className="alert-inner--text"><strong>Balance Not Availabel.</strong></span>
                            <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
            }
        </>
    )
}
const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
        getCustomerBalanceReducer: state.getCustomerBalanceReducer,
    }
}
const mapDispatchtoProps = {
    getCustomerBalanceAction: (id) => getCustomerBalanceAction(id),
}
export default connect(mapStateToProps, mapDispatchtoProps)(Balance)