import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { userDetailsUpdateAction, getUserDetailsAction } from '../Redux/Actions/Actions'
import FooterComponent from "./FooterComponent";
import NevbarComponents from "./NevbarComponents";

function UserProfileController(props) {
    const [userFormData, setUserFormData] = useState();

    useEffect(() => {
        props.getUserDetailsAction();
    }, [])

    useEffect(() => {
        if (props.loginUserDetailsReducer?.loginUserDetails) {
            setUserFormData(props.loginUserDetailsReducer.loginUserDetails);
        }
    }, [props.loginUserDetailsReducer])

    const onFormUpdate = (event) => {
        setUserFormData({ ...userFormData, [event.target.name]: event.target.value });
    }

    const onSaveForm = (event) => {
        event.preventDefault()
        props.onUserDetailsUpdateAction(userFormData.id, userFormData);
    }

    return (
        <div className="">
            <NevbarComponents />
            <div className="border-light card shadow " id="tabs-icons-text-2" role="tabpanel"
                aria-labelledby="tabs-icons-text-2-tab">
                <div className="p-0 m-2">
                    <div className="card ">
                        <form onSubmit={onSaveForm} >
                            <div className="card-header bg-light h6 d-flex justify-content-between ">
                                <span className="card-title m-0">Profile Details</span>
                                <button type="submit" className="btn btn-primary btn-sm mr-3">Save</button>
                            </div>
                            {props.loginUserDetailsReducer?.loginUserDetails ?

                                <div className="card-body ">
                                    <h5 className="d-block font-weight-bold ">Personl Details</h5>
                                    <div className="row">
                                        <div className="col-lg-10 col-lg-10 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Full Name</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="Full Name" name="name" className="form-control"
                                                    value={userFormData ? userFormData.name : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Email Id</small>
                                            <div className="form-group">
                                                <input type="emil" placeholder="Email Id" name="email" className="form-control"
                                                    value={userFormData ? userFormData.email : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Contect Number</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="Contect Number" name="contactNo" className="form-control"
                                                    value={userFormData ? userFormData.contactNo : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                    </div>
                                    <h5 className="d-block font-weight-bold ">Card Details</h5>
                                    <div className="row">
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">GST Number</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="GST Number" name="gstNo" className="form-control"
                                                    value={userFormData ? userFormData.gstNo : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">PAN Number</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="PAN Number" name="panNo" className="form-control"
                                                    value={userFormData ? userFormData.panNo : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Adhar Number</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="Adhar Number" name="adharNo" className="form-control"
                                                    value={userFormData ? userFormData.adharNo : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                    </div>
                                    <h5 className="d-block font-weight-bold ">Addres Details</h5>
                                    <div className="row">
                                        <div className="col-lg-10 col-lg-10 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Compny Name</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="Compny Name" name="compnyName" className="form-control"
                                                    value={userFormData ? userFormData.compnyName : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-10 col-lg-10 col-sm-12">
                                            <small className="d-block  font-weight-bold ">Addres</small>
                                            <div className="form-group">
                                                <textarea type="text" placeholder="Address Line 1" name="address" className="form-control" onChange={onFormUpdate}
                                                    value={userFormData ? userFormData.address : ''}>
                                                    {userFormData ? userFormData.address : ''}
                                                </textarea>
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">City</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="City" name="city" className="form-control"
                                                    value={userFormData ? userFormData.city : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">State</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="State" name="state" className="form-control"
                                                    value={userFormData ? userFormData.state : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                        <div className="col-lg-5 col-md-6 col-sm-12">
                                            <small className="d-block  font-weight-bold ">PinCode</small>
                                            <div className="form-group">
                                                <input type="text" placeholder="PinCode" name="pincode" className="form-control"
                                                    value={userFormData ? userFormData.pincode : ''} onChange={onFormUpdate} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                : < div className="alert alert-default bg-warning fade show" role="alert">
                                    <span className="alert-inner--text"><strong>Please Wait, Updating Details. </strong></span>
                                    <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                            }
                        </form>
                    </div>
                </div>
            </div >
            <FooterComponent />
        </div>
    )

}

const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
    }
}

const mapDispatchtoProps = {
    onUserDetailsUpdateAction: (id, details) => userDetailsUpdateAction(id, details),
    getUserDetailsAction: () => getUserDetailsAction(),

}

export default connect(mapStateToProps, mapDispatchtoProps)(UserProfileController)