import React, { useEffect, useState } from "react";
import HeaderComponent from "./HeaderComponent";
import FooterComponent from "./FooterComponent";
import NevbarComponents from "./NevbarComponents";
import { connect } from "react-redux";

function HomeComponent(props) {

    console.log(props.loginUserDetailsReducer?.loginUserDetails);
    return (
        <div className="">
            <NevbarComponents />
            {props.loginUserDetailsReducer && props.loginUserDetailsReducer.loginUserDetails ?
                <>
                    <HeaderComponent />
                    {/* <FooterComponent /> */}
                </>
                : <div className="alert alert-default bg-warning fade show" role="alert">
                    <span className="alert-inner--text"><strong>User is not Login, Or Sesstion TimeOut  Plese Login Again.</strong></span>
                </div>
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        loginUserDetailsReducer: state.loginUserDetailsReducer,
    }
}
const mapDispatchtoProps = {
}

export default connect(mapStateToProps, mapDispatchtoProps)(HomeComponent)