import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import NevbarComponent from "./NevbarComponents";

export default function HeaderComponent() {
    return (
        <>
            {/* <NevbarComponent /> */}
            <div className="container-fluid">
                <div className="row my-2" >
                    <div className="col-3 px-1">
                        <Link className="btn btn-primary w-100 px-0" to={'/alllots'}>All Lots</Link>
                    </div>
                    <div className="col-3 px-1">
                        <Link className="btn btn-primary w-100 px-0" to={'/live'}>Live</Link>
                    </div>
                    <div className="col-3 px-1">
                        <Link className="btn btn-primary w-100 px-0" to={'/sold'}>Sold</Link>
                    </div>
                    <div className="col-3 px-1">
                        <Link className="btn btn-primary w-100 px-0" to={'/expired'}>Expired</Link>
                    </div>
                </div>
            </div>
        </>)
}

const mapStateToProps = (state) => {
    return {
        loginUserDetails: state.loginUserDetailsReducer.loginUserDetails,
    }
}
const mapDispatchtoProps = {
}
connect(mapStateToProps, mapDispatchtoProps)(HeaderComponent)